
label{
    text-align: center;
    display:block;
    padding: 1rem;
}
input{
    width:80%;
    border: 1px solid grey;
    border-radius: 2rem;
    box-shadow: 2px 2px 5px 5px lightgrey;
    margin: 0 10%;
    padding: 0 2rem;
}
.btn-input{
    background-color:  rgb(84, 243, 179);
    border-radius: 5rem;
    padding:1rem 2rem;
    color:white;
    font-size: 1.3rem;
    font-weight: bold;
   
}
.btn-input:hover{
    background-color:  rgb(84, 243, 179);
    border-radius: 10rem;
    padding:1.2rem 2rem;
    color:white;
   
}
