/* column preview template */
h3{
    font-size: 14px;
}
p{
    font-size: 10px;
    font-weight: bold;
}
h1,h3,p{
    display: inline-block;
    padding: 5px 10px;
}
.img{
    width: 140px;
    height: 140px;
    float: right;
    border-radius: 100px 100px;
    object-fit: cover;
}
.instituteName{
    display:block;
}

.col-preview{

    padding:5% 1%;
    
}
.preview{
    border:0.5px solid grey;
    padding: 2px;
}
.preview-heading-4{
    background-color:white;
    color:navy;
    margin:0 0 1rem;
    padding:2% 5%;
}
.preview-body{
    background-color: white;
    padding: 2% 5%;
    text-align: left;
}
.preview-h3{
    display: block;
    
}
.preview-title-h3{
       
    display: block;
    font-weight: bold;
    color:navy;
}

@media print { 
   .no-print{
       display: none;
   }
    .printme  { display: block;}
    form{
        display:none;
    }
    h1{
        font-size:18pt;
        font-weight:bold;
    }
    h3{
        font-size: 14pt;
        display:inline;
    }
    p{
        font-size: 8pt;
        font-weight: bold;
    }
    h1,h3,p{
        display: inline-block;
        padding: 5pt 10pt;
    }
    .img{
        width: 100pt;
        height: 100pt;
        float: right;
        border-radius: 50%;
        object-fit: cover;
    }
   
    .col-preview{
    
        padding:10pt 5pt;
        
    }
    .preview{
        border:0.5pt solid grey;
        border-radius: 5pt;
        padding: 1pt;
    }
    .preview-heading-4{
        background-color:  white;
        color:navy;
        margin:0 0 5pt;
        padding:20pt 50pt;
    }
    .preview-body{
        background-color: white;
        padding: 15pt 50pt;
        text-align: left;
    }.skill{
        color:rgb(86, 86, 214);
        font-size: 16pt;
        word-spacing: 30pt;

    }
    .preview-h3{
        display: block;
       
    }
    .preview-title-h3{
       
            display: block;
            font-weight: bold;
            color:navy;
        }
    
}